*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  
  font-family: 'Outfit', 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

p{
  font-size: 1.6rem;
}
h1{
  font-size: 6rem;
}

.App{
  width: 100%;
  height: 100vh;
  position: relative;
  background-color: rgba(0,0,0,0.1);
  color: rgb(10, 8, 8);
}
.App:before{
  content: '';
  background:url('./assets/wetter3.jpg') no-repeat center center/cover;
  position:absolute ;
  width: 100%;
  height: 100vh;
  top: 0;
  left: 0;
  z-index: -1;
}
.App .search{
  text-align: center;
  padding: 1rem;
}
.App .input{
  padding: .6rem 1.5rem;
  font-size: 1.2rem;
  border-radius: 25px;
  border: 1px solid rgba(17, 15, 15, 0.7);
  background-color: rgba(255,255,255,0.9);
  color: #070101;

}
.container{
  width:700px;
  height: 700px;
  margin:auto;
  padding: 0 1rem;
  position: relative;
  top: 10%;
  display: flex;
  flex-direction:column ;
  justify-content: space-between;


}
.App .top{
  width: 100%;
  margin: 1erm auto;

}
.App .description{
  position: relative;
  right: -90%;
  transform-origin: 0 0;
  transform: rotate(270deg);
}

.App .button{
  display: flex;
  justify-content: space-evenly;
  text-align: center;
  width: 100%;
  margin: 1rem auto;
  padding: 1rem;
  border-radius: 10px;
  background-color:rgba(225,225,225, 0.9) ;
}
.bold{
  font-size: 700;
}

::placeholder{
  color: #0a0909;
}
.clouds{
  color: #070101;
  font-size: 2rem;
  margin-top:50px;
}